@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500&display=swap');

html,
body {
  padding: 0;
  margin: 0;
  height: fit-content;
}

a {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
}

p {
  margin: 0;
  font-weight: 400;
}

* {
  box-sizing: border-box;
  font-family: 'Baloo 2', sans-serif !important;
}

div#wentilab-wave {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100px;
  margin: 0 auto;
  margin-top: 30px;
}
div#wentilab-wave .dot {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 8px;
  background: #004ebc;
  animation: wave 1.3s linear infinite;
}
div#wentilab-wave .dot:nth-child(2) {
  animation-delay: -1.1s;
}
div#wentilab-wave .dot:nth-child(3) {
  animation-delay: -0.9s;
}

@keyframes wave {
  0%,
  60%,
  100% {
    transform: initial;
  }
  30% {
    transform: translateY(-15px);
  }
}

.wentilabs-chat {
  z-index: 9999;
  border-radius: 8px;
  width: 350px;
  max-height: 500px;
  height: 500px;
  cursor: auto;
  opacity: 1;
}

.wentilabs-chat .chat-header {
  display: flex;
  align-items: center;
  height: 53px;
  background: #004ebc;
  padding: 12px 16px;
  border-top-left-radius: 12px;
  border-top-right-radius: 16px;
}

.wentilabs-chat .chat-header .header-title {
  flex-grow: 1;
  flex-shrink: 1;
  font-size: 18px;
  line-height: 28.84px;
}

.wentilabs-chat .chat-content {
  padding: 10px;
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
  flex-grow: 1;
  background: transparent;
}

.wentilabs-chat .chat-content .chat-message-wrapper {
  width: 100%;
  align-items: end;
  justify-content: end;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilabs-chat .chat-content .chat-message-wrapper-bot {
  width: 100%;
  align-items: start;
  margin-bottom: 20px;
  padding-right: 5px;
}

.wentilabs-chat .chat-content .chat-message-wrapper .chat-message-me {
  min-width: 80px;
  background-color: #004bec;
  color: white;
  padding: 12px 16px;
  border-radius: 20px 20px 5px 20px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilabs-chat .chat-content .chat-message-wrapper-bot .chat-message-bot {
  min-width: 80px;
  background-color: #f0f0f0;
  color: #1c1c1c;
  padding: 12px 16px;
  border-radius: 20px 20px 20px 5px;
  font-size: 16px;
  line-height: 19px;
  max-width: 250px;
}

.wentilabs-chat .chat-content .chat-message-wrapper-bot .chat-message-link-wrapper {
  background-color: #f0f0f0;
  padding: 12px;
  padding-bottom: 8px;
  border-radius: 15px;
  max-width: 250px;
}

.wentilabs-chat .chat-content .chat-message-wrapper-bot .chat-message-link-wrapper .chat-message-product-link {
  width: 100%;
  text-align: center;
  display: block;
  margin-top: 8px;
  color: #1c1c1c;
  font-size: 16px;
  line-height: 19px;
}

.wentilabs-chat .chat-footer {
  width: 100%;
  margin-top: 10px;
  color: black;
  padding: 10px;
  padding-bottom: 6px;
}

.wentilabs-chat .chat-footer .message-template-wrapper {
  width: 100%;
  justify-content: end;
  margin-bottom: 16px;
  cursor: pointer;
  gap: 10px;
  overflow-x: auto;
}

.wentilabs-chat .chat-footer .message-template-wrapper .message-template-item {
  background-color: #004ebc;
  color: white;
  padding: 8px;
  border-radius: 15px;
  font-size: 14px;
}

.wentilabs-chat .chat-footer .chat-input-group {
  width: 100%;
  background-color: #fafafa;
  height: 48px;
  border-radius: 45px;
  box-shadow: 0px 2px 4px 0px #0000001a;
  border: 1px solid #e6e6e6;
}

.wentilabs-chat .chat-footer .chat-input-group .chat-input {
  color: #1c1c1c;
  background: transparent;
  height: 48px;
  border-radius: 30px;
  border: none;
  margin-right: 16px;
  margin-left: 4px;
}

.wentilabs-chat .chat-footer .chat-input-group .chat-input::placeholder {
  color: #1c1c1c;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  opacity: 30%;
}

.wentilabs-chat .chat-footer .chat-input-group .chat-input-right-element {
  height: 48px;
  margin-right: 16px;
}

.wentilabs-chat .chat-footer .chat-powered-by {
  width: 100%;
  text-align: center;
  font-size: 9px;
  margin-top: 5px;
  line-height: 9px;
}

.wentilabs-chat .chat-footer .chat-disclamer {
  width: 100%;
  text-align: center;
  font-size: 10px;
  margin-top: 5px;
  line-height: 10px;
}

.wentilabs-chat .chat-footer .chat-powered-by .link {
  text-decoration: underline;
  color: #5e50a0;
  font-size: 10px;
}

.wentilabs-chat .wentilabs-chat-button {
  outline: 0;
  position: fixed;
  right: 25px;
  bottom: 90px;
  display: block;
  background: #004ebc;
  width: 47px;
  height: 47px;
  border: 0;
  border-radius: 100%;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 32px rgba(0, 0, 0, 0.16);
  transition: box-shadow 13200ms ease;
}
.wentilabs-chat .wentilabs-chat-button:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.09), 0 4px 40px rgba(0, 0, 0, 0.24);
}
.wentilabs-chat .wentilabs-chat-button .wentilabs-chat-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: top left;
}
.wentilabs-chat .wentilabs-chat-button .wentilabs-chat-icon--close {
  width: 14px;
  height: 14px;
}
.wentilabs-chat .wentilabs-chat-button .wentilabs-chat-icon--launcher {
  width: 32px;
  height: 36px;
}

.wentilabs-chat .wentilabs-chat-base {
  position: fixed;
  right: 25px;
  bottom: 145px;
  background: #fff;
  width: 360px;
  height: 100%;
  max-height: 650px;
  border-radius: 8px;
  box-shadow: 0 5px 40px rgba(0, 0, 0, 0.16);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.wentilabs-chat .wentilabs-chat-header {
  display: flex;
  align-items: center;
  height: 53px;
  background: #004ebc;
  padding: 12px 16px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  font-size: 18px;
  line-height: 28.84px;
  color: white;
}
